import React, { useState, Fragment } from "react";
import { Link } from "react-router-dom";

import { filterFunction } from "./Helpers/FilterFunction";
import { computeIndices } from "./Helpers/ComputeIndices";
import { getTableLabel } from "../../dataHelpers";

function DatabaseWideTabSearch(props) {
  const { data, numItemsPerPage, fieldsToSearch } = props;

  // Setup

  const [searchString, setSearchString] = useState("");

  const [filteredData, setFilteredData] = useState(data);

  const [numItemsInFilteredResults, setNumItemsInFilteredResults] = useState(
    data.length
  );
  const [numPages, setNumPages] = useState(
    Math.ceil(data.length / numItemsPerPage)
  );

  const [pageNumber, setPageNumber] = useState(1);
  const [startIndex, setStartIndex] = useState(1);
  const [endIndex, setEndIndex] = useState(
    Math.min(numItemsPerPage, numItemsInFilteredResults)
  );

  const [resultsInView, setResultsInView] = useState([
    ...data.filter((d, i) => i >= 0 && i < numItemsPerPage),
  ]);

  // Event Handlers

  const handleSearchBoxChange = (e) => {
    const newSearchString = e.target.value;
    setSearchString(newSearchString);

    const filteredResults = filterFunction(
      newSearchString,
      data,
      fieldsToSearch
    );

    setFilteredData(filteredResults);

    setNumItemsInFilteredResults(filteredResults.length);

    setNumPages(Math.ceil(filteredResults.length / numItemsPerPage));

    setPageNumber(Math.min(1, filteredResults.length));
    setStartIndex(Math.min(1, filteredResults.length));
    setEndIndex(Math.min(numItemsPerPage, filteredResults.length));
    setResultsInView([
      ...filteredResults.filter((d, i) => i >= 0 && i < numItemsPerPage),
    ]);
  };

  const handlePrevPageClick = () => {
    if (pageNumber > 1) {
      const nextPageNumber = pageNumber - 1;
      const { startIndex, endIndex } = computeIndices(
        nextPageNumber,
        numItemsPerPage,
        numItemsInFilteredResults,
        numPages
      );

      const resultsInView = filteredData.filter(
        (d, i) => i >= startIndex - 1 && i < endIndex
      );

      setPageNumber(nextPageNumber);
      setStartIndex(startIndex);
      setEndIndex(endIndex);
      setResultsInView(resultsInView);
    }
  };
  const handleNextPageClick = () => {
    if (pageNumber < numPages) {
      const nextPageNumber = pageNumber + 1;
      const { startIndex, endIndex } = computeIndices(
        nextPageNumber,
        numItemsPerPage,
        numItemsInFilteredResults,
        numPages
      );

      const resultsInView = filteredData.filter(
        (d, i) => i >= startIndex - 1 && i < endIndex
      );

      setPageNumber(nextPageNumber);
      setStartIndex(startIndex);
      setEndIndex(endIndex);
      setResultsInView(resultsInView);
    }
  };

  // Render Methods

  const renderRow = (datum) => {
    const database = datum["database"];
    const table_name = datum["table"];

    return (
      <tr key={`${database}-${table_name}-${datum.var_name}`}>
        <td>
          <Link to={`/variables-in-table/${database}/${table_name}`}>
            {getTableLabel(table_name, database)}
          </Link>
        </td>
        <td>{datum.var_name}</td>
        <td>{datum.var_label}</td>
      </tr>
    );
  };

  const renderTable = (data) => {
    return (
      <table>
        <thead>
          <tr>
            <th>Table</th>
            <th>Variable Name</th>
            <th>Variable Label</th>
          </tr>
        </thead>
        <tbody>{data.map((datum) => renderRow(datum))}</tbody>
      </table>
    );
  };

  const renderInformation = () => {
    return (
      <Fragment>
        <div className="col s6 center">
          On page {pageNumber} of {numPages}, viewing {startIndex} to {endIndex}{" "}
          of {numItemsInFilteredResults} results.
        </div>
      </Fragment>
    );
  };

  const renderPaginationButtons = () => {
    return (
      <ul className="pagination" style={{ userSelect: "none" }}>
        <li>
          <a onClick={handlePrevPageClick}>
            <i className="material-icons large">chevron_left</i>
          </a>
        </li>
        <li className="active grey darken-1">
          <a>{pageNumber}</a>
        </li>

        <li>
          <a onClick={handleNextPageClick}>
            <i className="material-icons large">chevron_right</i>
          </a>
        </li>
      </ul>
    );
  };

  const renderSearchBox = () => {
    return (
      <div className="col s4 center">
        <input
          onChange={handleSearchBoxChange}
          value={searchString}
          placeholder="Search..."
        />
      </div>
    );
  };

  // Main

  return (
    <Fragment>
      <div className="card">
        <div className="card-content">
          <div className="row">{renderTable(resultsInView)}</div>

          <div className="row"></div>
        </div>
      </div>
      <div className="row">
        {renderSearchBox()}
        {renderInformation()} {renderPaginationButtons()}
      </div>
    </Fragment>
  );
}

export default DatabaseWideTabSearch;
