import { tab } from "@testing-library/user-event/dist/tab";
import data from "./data/bundle.json";

export function getDatabaseLabel(database) {
  const { databases } = data;
  return databases.find((d) => d.database === database)["database_label"];
}

export function getTableLabel(table, database) {
  const { tables } = data;
  return tables.find((d) => (d.table === table) & (d.database === database))[
    "table_label"
  ];
}

export function getNumTablesInDatabase(database) {
  const { tables } = data;
  return tables.filter((d) => d["database"] === database).length;
}

export function getNumVariablesInDatabase(database) {
  const { variables } = data;
  return variables.filter((d) => d["database"] === database).length;
}

export function getTableLevelMetadata(database) {
  const { tables } = data;
  return tables.filter((d) => d["database"] === database);
}

export function getDatabaseWideVariableLevelMetadata(database) {
  const { variables } = data;
  return variables.filter((d) => d["database"] === database);
}

export function getTableWideVariableLevelMetadata(database, table) {
  const { variables } = data;
  return variables.filter(
    (d) => (d["database"] === database) & (d["table"] === table)
  );
}
