import React from "react";

import { useParams } from "react-router-dom";

import {
  getTableWideVariableLevelMetadata,
  getDatabaseLabel,
  getTableLabel,
} from "../../dataHelpers";

import TabSearchWithPag from "./TabSearchWithPag";

function TableWideVariableLevelMetadata() {
  const params = useParams();
  const { database, table } = params;
  const allData = getTableWideVariableLevelMetadata(database, table);
  console.log({ allData, database, table });
  return (
    <div className="card">
      <div className="card-content">
        <span className="card-title">
          Viewing Variables in the <b>{getTableLabel(table, database)}</b> Table
          of the <b>{getDatabaseLabel(database)}</b> Database
        </span>

        <TabSearchWithPag
          data={allData} // the data in the table
          colNames={["var_name", "var_label"]} // the names of the columns as they appear in the data
          colLabels={{
            var_name: "Variable Name",
            var_label: "Variable Label",
          }} // labels for the columns
          codeCols={[]} // these columns live in <code> elements
          numItemsPerPage={5} // number of items to show per page
          fieldsToSearch={["var_name", "var_label"]}
        ></TabSearchWithPag>
      </div>
    </div>
  );
}

export default TableWideVariableLevelMetadata;
