import "materialize-css/dist/css/materialize.min.css";
import "materialize-css/dist/js/materialize.min.js";

import React from "react";
import ReactDOM from "react-dom/client";

import { BrowserRouter, Routes, Route, Navigate, Link } from "react-router-dom";

import App from "./App";
import Index from "./Components/Index";
import TableLevelMetadata from "./Components/TableLevel/TableLevelMetadata";

import DatabaseWideVariableLevelMetadata from "./Components/DatabaseWideVariables/DatabaseWideVariableLevelMetadata";
import TableWideVariableLevelMetadata from "./Components/TableWideVariables/TableWideVariableLevelMetadata";

const root = ReactDOM.createRoot(document.getElementById("root"));

root.render(
  <React.StrictMode>
    <BrowserRouter>
      <Routes>
        <Route exact path="/" element={<App />}>
          <Route index element={<Index></Index>} />
          <Route path="/index.html" element={() => <Navigate to="/404" />} />
          <Route path="/tables/:key" element={<TableLevelMetadata />} />
          <Route
            path="/variables-in-database/:database"
            element={<DatabaseWideVariableLevelMetadata />}
          />
          <Route
            path="/variables-in-table/:database/:table"
            element={<TableWideVariableLevelMetadata />}
          />

          <Route path="*" element={<Navigate replace to="/" />} />
        </Route>
      </Routes>
    </BrowserRouter>
  </React.StrictMode>
);
