export const filterFunction = (queryString, itemList, fieldsToSearch) => {
  // Return all items if there is no queryString
  if (queryString === "") {
    return itemList;
  }

  // Set up an array containing the items we will keep
  const itemsToKeep = [];

  // Split the query string
  const queryStringKeywords = queryString.split(" ");

  // Turn each word into lowercase
  const lowerCaseQueryStringWords = queryStringKeywords.map((word) =>
    word.toLowerCase()
  );

  for (const item of itemList) {
    for (const field of fieldsToSearch) {
      for (const word of lowerCaseQueryStringWords) {
        if (item[field]) {
          if (item[field].toLowerCase().includes(word)) {
            itemsToKeep.push(item);
          }
        }
      }
    }
  }

  // Return unique items
  return [...new Set(itemsToKeep)];
};
