import { Fragment } from "react";
import { Link } from "react-router-dom";

import {
  getDatabaseLabel,
  getNumTablesInDatabase,
  getNumVariablesInDatabase,
} from "../dataHelpers";

const renderCard = (database) => {
  return (
    <div className="card">
      <div className="card-content">
        <span className="card-title">{getDatabaseLabel(database)}</span>
        View{" "}
        <Link to={`/variables-in-database/${database}`}>
          all {getNumVariablesInDatabase(database)} Variables
        </Link>{" "}
        that exist in the{" "}
        <Link to={`/tables/${database}`}>
          {getNumTablesInDatabase(database)} Tables
        </Link>{" "}
        of the {getDatabaseLabel(database)} database.
      </div>
    </div>
  );
};

function Index() {
  return (
    <Fragment>
      <div className="card">
        <div className="card-content">
          <h4 className="center">Data Warehouse</h4>
          <div className="row">
            <div className="col s6">{renderCard("personal")}</div>
            <div className="col s6">{renderCard("education")}</div>
          </div>
        </div>
      </div>
    </Fragment>
  );
}

export default Index;
