import React from "react";

import { useParams } from "react-router-dom";

import DatabaseWideTabSearch from "./DatabaseWideTabSearch";

import {
  getDatabaseWideVariableLevelMetadata,
  getDatabaseLabel,
} from "../../dataHelpers";

function DatabaseWideVariableLevelMetadata() {
  const params = useParams();
  const { database } = params;
  const allData = getDatabaseWideVariableLevelMetadata(database);

  return (
    <div className="row">
      <div className="col s12">
        <div className="card">
          <div className="card-content">
            <span className="card-title">
              Viewing Variables in the <b>{getDatabaseLabel(database)}</b>{" "}
              Database
            </span>
            <DatabaseWideTabSearch
              data={allData}
              numItemsPerPage={5}
              fieldsToSearch={["table_name", "var_name", "var_label"]} // the data in the table
            ></DatabaseWideTabSearch>
          </div>
        </div>
      </div>
    </div>
  );
}

export default DatabaseWideVariableLevelMetadata;
