import React from "react";

import { useParams } from "react-router-dom";

import { getTableLevelMetadata, getDatabaseLabel } from "../../dataHelpers";

import TableTabSearchWithPag from "./TableTabSearchWithPag";

function TableLevelMetadata() {
  const params = useParams();
  const { key } = params;

  const allData = getTableLevelMetadata(key);

  return (
    <div className="card">
      <div className="card-content">
        <span className="card-title">
          Viewing Tables from the <b>{getDatabaseLabel(key)}</b> Database
        </span>

        <TableTabSearchWithPag
          data={allData}
          numItemsPerPage={5}
          fieldsToSearch={["table_name", "num_vars"]}
        ></TableTabSearchWithPag>
      </div>
    </div>
  );
}

export default TableLevelMetadata;
